<template>
    <div class="sections__item">
        <div class="sections__title">{{ sectionName }}</div>
        <SectionItem
            @delete-section-item="deleteSectionItem"
            @generate-output-code="this.$emit('generate-output-code', sectionName, section.createdFields)"
            v-for="createdField in section.createdFields" :key="createdField.id"
            v-bind:createdField="createdField"
            v-bind:fields="section.fields"
        />
        <button class="btn-dotted" v-bind:class="{'no-item': section.createdFields.length === 0}"
                @click="addSectionItem(section.fields)">Добавить
        </button>
    </div>
</template>

<script>
import SectionItem from "@/components/SectionItem";

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: "Section",
    props: ["section", "sectionName"],
    components: {
        SectionItem
    },
    methods: {
        addSectionItem(fields) {
            const newSectionItem = {
                id: Date.now(),
            }

            fields.forEach((item) => {
                newSectionItem[item.name] = {
                    value: ""
                }
            });

            // eslint-disable-next-line vue/no-mutating-props
            this.section.createdFields.push(newSectionItem)
            this.$emit('generate-output-code', this.sectionName, this.section.createdFields)
        },
        deleteSectionItem(sectionItemId) {
            // eslint-disable-next-line vue/no-mutating-props
            this.section.createdFields = this.section.createdFields.filter(item => item.id !== sectionItemId)
            this.$emit('generate-output-code', this.sectionName, this.section.createdFields)
        }
    }
}
</script>

<style scoped>
.sections__item{
    display: inline-block;
    width: 100%;
}

.sections__title {
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 30px;
}

.btn-dotted.no-item {
    margin-top: 0;
}
</style>