<template>
    <label class="text-reader btn-dotted">
        Загрузить из файла
        <input type="file" id="file" @change="handleFileUpload($event)"/>
    </label>
</template>

<script>
export default {
    name: "FileUpload",
    methods: {
        handleFileUpload(input) {
            let file = input.target.files[0];
            let reader = new FileReader();

            reader.readAsText(file);
            reader.onload = e => this.$emit("load", e.target.result);
            reader.onerror = function () {
                alert(reader.error);
            };
        }
    }
}
</script>

<style scoped>
.text-reader {
    position: relative;
    overflow: hidden;
    display: inline-block;
    cursor: pointer;
}

.text-reader input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0;
}

.btn-dotted {
    width: 100%;
    box-sizing: border-box;
    margin-top: 0;
    margin-bottom: 20px;
}
</style>