}

.section-item__icon svg{
    width: 20px;
    height: 20px;
}

.section-item input {
    border-radius: 5px;
    width: 100%;
    margin-bottom: 10px;
    background-color: var(--light-grey);
    padding: 15px;
    box-sizing: border-box;
    border: 0;
    font-size: 14px;
}

.section-item input::placeholder {
    color: var(--dark-grey);
}

.section-item input:focus {
    outline: none;
}
</style>